:root{
  --sidebar-w: 280px;

  /* Palette */
  --navy-900:#0A1F44;  /* headings, strong text */
  --navy-700:#163E72;  /* body text */
  --navy-500:#2E5E9E;  /* subtle accents/links */
  --bg:#ffffff;        /* app background */
  --card:#dfdfdf;      /* surfaces/cards */
  --card-sub:#fbfcff;  /* lighter surface */
  --border:#e6eaf2;    /* borders/dividers */
  --muted:#6b7a99;     /* secondary text */

  --green:#17c964;     /* primary accent */
  --green-600:#169a52; /* hover/active */
  --green-50:#eafaf1;  /* soft fill */

  --radius:14px;
  --radius-lg:10px;
  --shadow:0 2px 10px rgba(10,31,68,.08);
}

/* base */
*{box-sizing:border-box}
html,body,#root{height:100%}
body{
  margin-left:15%;
  background:var(--bg);
  color:var(--navy-700);
  font-family:Inter,ui-sans-serif,system-ui;
}

/* layout */
.dashboard{position:relative;min-height:100vh;background:var(--bg)}
.main-content{
  position:relative;
  padding:24px;
  margin-left:0; /* your Sidebar handles spacing */
}
@media (min-width:1024px){
  .main-content{padding:28px 32px}
}

/* header */
.page-header{
  display:flex;align-items:center;justify-content:space-between;gap:16px;
  margin-bottom:18px;
}
.page-header h1{
  margin:0;color:var(--navy-900);font-weight:700;letter-spacing:.2px;
}

/* hamburger (mobile) */
.hamburger{
  position:fixed;top:14px;left:14px;z-index:60;
  width:42px;height:42px;border-radius:12px;
  background:var(--card);box-shadow:var(--shadow);display:grid;place-items:center;
  color:var(--navy-700);cursor:pointer;border:1px solid var(--border);
}
@media (min-width:1024px){ .hamburger{display:none} }

/* sidebar overlay (mobile) */
.sidebar-backdrop{
  position:fixed;inset:0;background:rgba(10,31,68,.35);backdrop-filter:blur(1px);z-index:50;
}

/* status pill */
.mikrotik-status{
  display:inline-flex;align-items:center;gap:10px;padding:10px 14px;
  border-radius:999px;font-size:.9rem;border:1px solid var(--border);
  background:var(--card);color:var(--navy-700);
}
.mikrotik-status .dot{
  width:9px;height:9px;border-radius:50%;
  background:#a8b3c5;display:inline-block;
}
.mikrotik-status.online{border-color:#cfeedd;background:var(--green-50)}
.mikrotik-status.online .dot{background:var(--green)}
.mikrotik-status.offline{border-color:#e7eaf0;background:#fafbff}

/* stat cards container (works even if component injects its own) */
.stats-cards, .quick-counters{
  display:grid;grid-template-columns:repeat(4,minmax(0,1fr));
  gap:14px;margin:14px 0 8px;
}
@media (max-width:1100px){ .stats-cards, .quick-counters{grid-template-columns:repeat(2,1fr)} }
@media (max-width:640px){ .stats-cards, .quick-counters{grid-template-columns:1fr} }

/* quick counters */
.quick-counters .counter{
  background:var(--card);border-radius:var(--radius);
  box-shadow:var(--shadow);padding:16px 18px;
}
.counter-title{font-size:.85rem;color:var(--muted);margin-bottom:6px}
.counter-value{
  font-size:1.6rem;font-weight:800;color:var(--green);
}
.quick-counters .counter:hover{border-color:#dbe8f7}

/* sections as white cards */
.pppoe-status-section,
.due-soon-section,
.expired-section,
.chart-container{
  background:var(--card); border-radius:var(--radius-lg);
  box-shadow:var(--shadow);padding:18px;margin-top:18px;
}
.section-head{display:flex;justify-content:space-between;align-items:center;gap:12px;margin-bottom:10px}
.section-head h2{margin:0;color:var(--navy-900);font-size:1.05rem}

/* â€œInclude Hotspotâ€ toggle â€“ keep native but branded */
.toggle input{accent-color:var(--green)}
.toggle{display:flex;align-items:center;gap:8px;color:var(--navy-700);font-size:.95rem}

/* tables */
.table-wrapper{overflow:auto;border-radius:12px}
table{width:100%;border-collapse:separate;border-spacing:0;min-width:820px}
thead th{
  text-align:left;padding:12px 14px;background:var(--card-sub);
  color:var(--navy-700)var(--border);font-weight:700;
}
tbody td{padding:12px 14px;color:var(--navy-700)}
tbody tr:hover{background:#f6fbf8} /* soft green hover */
tfoot td{
  padding:12px 14px;background:var(--card-sub);color:var(--navy-900)
}

/* buttons (global green) */
button, .btn{
  background:var(--green);color:#fff;border:none;border-radius:12px;padding:10px 14px;
  font-weight:600;cursor:pointer;transition:transform .06s ease, background .15s ease;
}
button:hover,.btn:hover{background:var(--green-600)}
button:active,.btn:active{transform:translateY(1px)}
@media(max-width:640px){
  body{
    margin: 0;  }
}
